import React, { ReactElement } from 'react';
import Home from '../Home';
import { Route, Switch } from 'react-router-dom';

export default function Routes(): ReactElement {
  return (
    <Switch>
      <Route exact path={'/'}>
        <Home />
      </Route>
      <Route exact path={'/portfolio'}>
        <Home />
      </Route>
    </Switch>
  );
}
