import paper from 'paper';
import SimplexNoise from 'simplex-noise';

export default function customCursor(): void {
  let clientX = window.innerWidth / 2;
  let clientY = (window.innerHeight * 15) / 100;
  const innerCursor = <HTMLElement>document.querySelector('.cursor-small');

  const initCursor = () => {
    document.addEventListener('mousemove', (e) => {
      clientX = e.clientX;
      clientY = e.clientY;
    });

    const render = () => {
      innerCursor.style.transform = `translate(${clientX}px, ${clientY}px)`;

      requestAnimationFrame(render);
    };
    requestAnimationFrame(render);
  };

  initCursor();

  let lastX = window.innerWidth / 2;
  let lastY = (window.innerHeight * 15) / 100;
  let isStuck = false;
  let group: paper.Group, stuckX: number, stuckY: number;

  const initCanvas = () => {
    const canvas = <HTMLCanvasElement>document.querySelector('.cursor-canvas');
    const shapeBounds = {
      width: 75,
      height: 75,
    };
    paper.setup(canvas);
    const strokeColor = 'rgba(255,193,7, 0.7)';
    const strokeWidth = 1;
    const segments = 8;
    const radius = 15;

    const noiseScale = 150;
    const noiseRange = 4;
    let isNoisy = false;

    const polygon = new paper.Path.RegularPolygon(new paper.Point(0, 0), segments, radius);
    polygon.strokeColor = <paper.Color>(<unknown>strokeColor);
    polygon.strokeWidth = strokeWidth;
    polygon.opacity = 1;
    polygon.smooth();
    group = new paper.Group([polygon]);
    group.applyMatrix = false;

    const noiseObjects = polygon.segments.map(() => new SimplexNoise());
    let bigCoordinates: number[][] = [];

    const lerp = (a: number, b: number, n: number) => {
      return (1 - n) * a + n * b;
    };

    const map = (value: number, in_min: number, in_max: number, out_min: number, out_max: number) => {
      return ((value - in_min) * (out_max - out_min)) / (in_max - in_min) + out_min;
    };

    paper.view.onFrame = (event: { count: number }) => {
      if (!isStuck) {
        lastX = lerp(lastX, clientX, 0.2);
        lastY = lerp(lastY, clientY, 0.2);
        group.position = new paper.Point(lastX, lastY);
      } else if (isStuck) {
        lastX = lerp(lastX, stuckX, 0.2);
        lastY = lerp(lastY, stuckY, 0.2);
        group.position = new paper.Point(lastX, lastY);
      }

      if (isStuck && polygon.bounds.width < shapeBounds.width) {
        polygon.scale(1.08);
      } else if (!isStuck && polygon.bounds.width > 30) {
        if (isNoisy) {
          polygon.segments.forEach((segment, i) => {
            segment.point.set(bigCoordinates[i][0], bigCoordinates[i][1]);
          });
          isNoisy = false;
          bigCoordinates = [];
        }
        const scaleDown = 0.92;
        polygon.scale(scaleDown);
      }

      if (isStuck && polygon.bounds.width >= shapeBounds.width) {
        isNoisy = true;
        if (bigCoordinates.length === 0) {
          polygon.segments.forEach((segment, i) => {
            bigCoordinates[i] = [segment.point.x, segment.point.y];
          });
        }

        polygon.segments.forEach((segment, i) => {
          const noiseX = noiseObjects[i].noise2D(event.count / noiseScale, 0);
          const noiseY = noiseObjects[i].noise2D(event.count / noiseScale, 1);

          const distortionX = map(noiseX, -1, 1, -noiseRange, noiseRange);
          const distortionY = map(noiseY, -1, 1, -noiseRange, noiseRange);

          const newX = bigCoordinates[i][0] + distortionX;
          const newY = bigCoordinates[i][1] + distortionY;
          segment.point.set(newX, newY);
        });
      }
      polygon.smooth();
    };
  };

  initCanvas();

  const initHovers = () => {
    const handleMouseEnter = (e: { currentTarget: Element }) => {
      const navItem = e.currentTarget;
      const navItemBox = navItem?.getBoundingClientRect();
      stuckX = Math.round(navItemBox.left + navItemBox.width / 2);
      stuckY = Math.round(navItemBox.top + navItemBox.height / 2);
      isStuck = true;
    };
    const handleMouseLeave = () => {
      isStuck = false;
    };
    const linkItems = document.querySelectorAll('button, a');
    linkItems.forEach((item) => {
      item.addEventListener('mouseenter', (handleMouseEnter as unknown) as EventListenerObject);
      item.addEventListener('mouseleave', handleMouseLeave);
    });
  };

  initHovers();
}
