import { AppBar, Button, Toolbar, Typography } from '@material-ui/core';
import { Link, useLocation } from 'react-router-dom';
import React, { ReactElement } from 'react';
import useStyles from './styles';

const MyAppBar: () => ReactElement = () => {
  const classes = useStyles();
  const currentRoute = useLocation().pathname;
  const isCurrent = (path: string) => {
    if (currentRoute === path) return 'active';
    return '';
  };
  return (
    <AppBar position={'relative'} className={classes.root}>
      <Toolbar className={classes.toolbarA}>
        <div className={classes.avatarIcon} />
        <Typography className={classes.toolbarAText}>EL NINO</Typography>
      </Toolbar>
      <Toolbar className={classes.toolbarB}>
        <Button component={Link} to={'/'} className={`${classes.Button} ${isCurrent('/')}`} replace>
          Home
        </Button>
        <Button component={Link} to={'/portfolio'} className={`${classes.Button} ${isCurrent('/portfolio')}`} replace>
          Portfolio
        </Button>
      </Toolbar>
    </AppBar>
  );
};

export default MyAppBar;
