import React, { ReactElement } from 'react';
import { Button, Grid, IconButton } from '@material-ui/core';
import useStyles from './styles';
import './animation.css';
import { Facebook, Instagram, LinkedIn, Telegram, Twitter } from '@material-ui/icons';
import Typewriter from 'typewriter-effect';
import AboutMe from './AboutMe';
import WhatIDo from './WhatIDo';

export default function Home(): ReactElement {
  const classes = useStyles();
  const onSocialButtonClick = (name: string) => () => {
    switch (name) {
      case 'linkedin':
        window.open('https://www.linkedin.com/in/eL7nino');
        break;
      case 'facebook':
        window.open('https://www.facebook.com/eL7n1no');
        break;
      case 'instagram':
        window.open('https://www.instagram.com/eL7n1no');
        break;
      case 'telegram':
        window.open('https://t.me/el7nino');
        break;
      case 'twitter':
        window.open('https://twitter.com/eL7n1no');
        break;
    }
  };

  return (
    <>
      <div className={classes.headerContent}>
        <div className={`${classes.headerInfo} el-avatar wow headerInfoAvatarAnim`} data-wow-duration={'1.5s'}>
          <div className={`${classes.headerInfoAvatarWrapper} wow avatarWrapperAnim`} data-wow-duration={'1.5s'}>
            <div className={`${classes.headerInfoAvatar} wow avatarAnim`} data-wow-duration={'1.5s'} />
          </div>
        </div>
        <div className={classes.headerInfo}>
          <div className={classes.headerInfoTextWrapper}>
            <div className={`${classes.headerInfoText} ${classes.headerInfoTextHola}`}>Hola, I am</div>
            <div className={`${classes.headerInfoText} ${classes.headerInfoTextName}`}>Nino</div>
            <div className={`${classes.headerInfoText} ${classes.headerInfoTextAbout}`}>
              <Typewriter
                options={{ loop: true }}
                onInit={(typewriter) => {
                  setTimeout(
                    () => typewriter.typeString('Software Engineer').pauseFor(1000).deleteAll().changeDelay(50).typeString('Code is a big part of my life').pauseFor(1000).start(),
                    3000,
                  );
                }}
              />
            </div>
            <Grid container spacing={2} className={classes.headerInfoButtonWrapper}>
              <Grid item>
                <Button className={`${classes.headerInfoButton} download-cv`}>Download CV</Button>
              </Grid>
              <Grid item>
                <Button className={`${classes.headerInfoButton} contact-me`} href={'mailto:eL7nino@outlook.com'}>
                  Contact Me
                </Button>
              </Grid>
            </Grid>
          </div>
        </div>
      </div>
      <div className={classes.socialWrapper}>
        <IconButton style={{ color: '#777777' }} onClick={onSocialButtonClick('linkedin')}>
          <LinkedIn style={{ fontSize: 40 }} />
        </IconButton>
        <IconButton style={{ color: '#777777' }} onClick={onSocialButtonClick('facebook')}>
          <Facebook style={{ fontSize: 40 }} />
        </IconButton>
        <IconButton style={{ color: '#777777' }} onClick={onSocialButtonClick('instagram')}>
          <Instagram style={{ fontSize: 40 }} />
        </IconButton>
        <IconButton style={{ color: '#777777' }} onClick={onSocialButtonClick('telegram')}>
          <Telegram style={{ fontSize: 40 }} />
        </IconButton>
        <IconButton style={{ color: '#777777' }} onClick={onSocialButtonClick('twitter')}>
          <Twitter style={{ fontSize: 40 }} />
        </IconButton>
      </div>
      <AboutMe />
      <WhatIDo />
    </>
  );
}
