import React, { ReactElement, useEffect, useState } from 'react';
import { HashRouter } from 'react-router-dom';
import useStyles from './styles';
import BaseParticles from 'react-particles-js';
import MyAppBar from '../MyAppBar';
import './animation.css';
import Routes from '../Routes';
import { detect } from 'detect-browser';
import { Button, Divider, Typography, useMediaQuery, useTheme } from '@material-ui/core';
import customCursor from '../../modules/customCursor';

const Particles = () => {
  return (
    <BaseParticles
      params={{
        particles: {
          color: { value: '#222222' },
          number: { value: 70 },
          size: { value: 4 },
          opacity: { value: 1 },
          line_linked: {
            opacity: 1,
            color: '#222222',
            frequency: 1,
            width: 1,
            shadow: {
              enable: true,
              blur: 1,
            },
          },
        },
      }}
      style={{
        left: 0,
        right: 0,
        top: 0,
        bottom: 0,
        position: 'fixed',
        zIndex: -1,
      }}
    />
  );
};

export default function App(): ReactElement {
  const classes = useStyles();
  const browser = detect();
  const theme = useTheme();
  const [browserData, setBrowserData] = useState({ isSupported: true, message: '' });
  const mdWidth = useMediaQuery(theme.breakpoints.up('md'));

  useEffect(() => {
    mdWidth && setTimeout(() => customCursor(), 1900);
  }, [mdWidth]);

  useEffect(() => {
    switch (browser && browser.name) {
      case 'chrome':
      case 'firefox':
      case 'edge':
      case 'edge-chromium':
      case 'samsung':
      case 'opera':
        break;
      default:
        setBrowserData({
          isSupported: false,
          message: 'Browser is not supported. Please use Chrome, Firefox or Edge instead.',
        });
    }
  }, []);

  return (
    <HashRouter basename={process.env.PUBLIC_URL}>
      <div className={classes.root}>
        {(!browserData.isSupported || window.innerWidth >= 960) && <Particles />}
        {browserData.isSupported ? (
          <div className={classes.wrapper}>
            <div className={`${classes.shapeA} wow shapeAAnim`} data-wow-duration={'1.5s'} />
            <MyAppBar />
            <Routes />
            <div className={classes.footer}>
              <Typography className={'copyright'}>Â© 2020 El Nino, All Rights Reserved.</Typography>
            </div>
          </div>
        ) : (
          <div className={classes.unsupportedBrowserContainer}>
            <Typography variant="h6" className={'unsupportedBrowserHeader'}>
              Oops !!
            </Typography>
            <Divider variant={'middle'} />
            <Typography className={'unsupportedBrowserBody'}>{browserData.message}</Typography>
            <div className={'unsupportedBrowserFooter'}>
              <Button onClick={() => setBrowserData({ isSupported: true, message: '' })}>Continue Anyway</Button>
            </div>
          </div>
        )}
      </div>
    </HashRouter>
  );
}
